<template>
  <div>
    <loading :active.sync="isLoading" color="red" backgroundColor="black">
      <template slot="default">
        <Reactor/>
      </template>
    </loading>
    <div class="row mt-3 p-2 bg-dark">
      <div class="col-md-2 mb-4" v-for="item in products" :key="item.id">
        <div class="card border border-blood shadow-sm">
          <div
            class="goodsimg"
            style="height: 300px; background-size: cover; background-position:top"
            :style="{'backgroundImage' : `url(${item.imageUrl})`}"
          ></div>
          <div class="card-body">
            <span
              class="badge badge-secondary float-right ml-2 mt-1"
              v-if="item.category === '收藏裝甲'"
            >{{item.category}}</span>
            <span
              class="badge badge-primary float-right ml-2 mt-1"
              v-else-if="item.category === '電影出現'"
            >{{item.category}}</span>
            <span class="badge badge-iron float-right ml-2 mt-1" v-else>{{item.category}}</span>
            <h5 class="card-title">
              <small class="text-dark">{{item.title}}</small>
            </h5>
            <small class="card-text">{{item.description}}</small>
            <div class="d-flex justify-content-between align-items-baseline">
              <del
                class="h6"
                v-if="item.origin_price != item.price"
              >原價{{item.origin_price | currency}}</del>
              <div
                class="h5"
                :class="{'ml-auto' :'item.origin_price != item.price','text-danger' :item.origin_price != item.price}"
              >{{item.price | currency}}</div>
            </div>
          </div>
          <div class="card-footer d-flex p-1">
            <button
              type="button"
              class="btn btn-outline-secondary btn-sm"
              @click="getProduct(item.id)"
            >
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              查看更多
            </button>
            <button
              type="button"
              class="btn btn-outline-danger btn-sm ml-auto"
              @click="addCart(item.id)"
            >
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <!-- 查看更多 Modal -->
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header border-0 mx-auto">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" height="100px">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price | currency }} 元</div>
              <del
                class="h6"
                v-if="product.origin_price != product.price"
              >原價 {{ product.origin_price | currency }} 元</del>
              <div
                class="h4"
                :class="{'ml-auto' :'product.origin_price != product.price','text-danger' :product.origin_price != product.price}"
              >現在只要 {{ product.price | currency }} 元</div>
            </div>
            <select name class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">選購 {{num}} {{product.unit}}</option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
              小計
              <strong>{{ product.num * product.price }}</strong> 元
            </div>
            <button
              type="button"
              class="btn btn-primary"
              @click="addCart(product.id , product.num )"
            >
              <i class="fas fa-spinner fa-spin"></i>
              加到購物車
            </button>
          </div>
        </div>
      </div>
    </div>

    <Pagination
      v-if="pagination.current_page != 0"
      :pageData="pagination"
      v-on:changepage="changePage"
    ></Pagination>

    <div class="my-5 row justify-content-center">
      <!-- 購物車 -->
      <div class="my-5 row justify-content-center">
        <table class="table text-white">
          <thead>
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in cart.carts" :key="item.id">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-danger btn-sm"
                  @click="delCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">已套用優惠券</div>
              </td>
              <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total | currency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="3" class="text-right">總計</td>
              <td class="text-right">{{ cart.total | currency }}</td>
            </tr>
            <tr>
              <td colspan="3" class="text-right text-success">折扣價</td>
              <td class="text-right text-success">{{ cart.final_total | currency}}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm">
          <input type="text" class="form-control" placeholder="請輸入優惠碼" v-model="coupon_code">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" type="button" @click="useCoupon">套用優惠碼</button>
          </div>
        </div>
      </div>
    </div>

    <div class="my-5 row justify-content-center">
      <form class="col-md-6 text-white" @submit.prevent="creatOrder">
        <div class="form-group">
          <label for="useremail">Email</label>
          <input
            type="email"
            class="form-control"
            :class="{'is-invalid':errors.has('email')}"
            name="email"
            id="useremail"
            v-model="form.user.email"
            placeholder="請輸入 Email"
            v-validate="'required|email'"
            required
          >
          <span class="text-danger" v-if="errors.has('email')">必須輸入E-mail</span>
        </div>

        <div class="form-group">
          <label for="username">收件人姓名</label>
          <input
            type="text"
            class="form-control"
            :class="{'is-invalid':errors.has('name')}"
            name="name"
            id="username"
            v-model="form.user.name"
            placeholder="輸入姓名"
            v-validate="'required'"
            required
          >
          <span class="text-danger" v-if="errors.has('name')">必須輸入姓名</span>
        </div>

        <div class="form-group">
          <label for="usertel">收件人電話</label>
          <input
            type="tel"
            class="form-control"
            id="usertel"
            v-model="form.user.tel"
            placeholder="請輸入電話"
          >
        </div>

        <div class="form-group">
          <label for="useraddress">收件人地址</label>
          <input
            type="text"
            class="form-control"
            :class="{'is-invalid':errors.has('address')}"
            name="address"
            id="useraddress"
            v-model="form.user.address"
            placeholder="請輸入地址"
            v-validate="'required'"
            required
          >
          <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
        </div>

        <div class="form-group">
          <label for="comment">留言</label>
          <textarea
            name
            id="comment"
            class="form-control"
            cols="30"
            rows="10"
            v-model="form.message"
          ></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger">送出訂單</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination";
import Reactor from "../Reactor";
export default {
  components: {
    Pagination,
    Reactor
  },
  data() {
    return {
      products: [],
      product: {},
      status: {
        loadingItem: ""
      },
      cart: {},
      isLoading: false,
      pagination: {},
      coupon_code: "",
      form: {
        user: {
          name: "",
          email: "",
          tel: "",
          address: ""
        },
        message: ""
      }
    };
  },
  methods: {
    getProducts(page = 1) {
      //如果有傳入用傳入值，空白為設定值
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        console.log(response.data);
      });
    },
    getProduct(id) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/product/${id}`;
      const vm = this;
      vm.status.loadingItem = id;
      this.$http.get(api).then(response => {
        vm.product = response.data.product;
        $("#productModal").modal("show");
        vm.product.num = 1;
        vm.status.loadingItem = "";
        console.log(response.data);
      });
    },
    addCart(id, qty = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty
      };
      this.$http.post(api, { data: cart }).then(response => {
        vm.status.loadingItem = "";
        $("#productModal").modal("hide");
        console.log(response.data);
        vm.getCart();
      });
    },
    getCart() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.isLoading = false;
        vm.cart = response.data.data;
        console.log(response.data);
      });
    },
    delCartItem(id) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(api).then(response => {
        vm.isLoading = false;
        vm.getCart();
        console.log(response.data);
      });
    },
    useCoupon() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`;
      const vm = this;
      const coupon = {
        code: vm.coupon_code
      };
      vm.isLoading = true;
      this.$http.post(api, { data: coupon }).then(response => {
        vm.isLoading = false;
        vm.getCart();
        console.log(response.data);
      });
    },
    creatOrder() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const vm = this;
      const order = vm.form;
      vm.isLoading = true;
      this.$http.post(api, { data: order }).then(response => {
        vm.isLoading = false;
        if (response.data.success) {
          vm.$router.push(` `);
        }
        console.log("ok", response.data);
      });
    },
    changePage(page) {
      this.getProducts(page);
    }
  },
  created() {
    this.getProducts();
    this.getCart();
  }
};
</script>




