<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table table-sm mt-5 table-hover text-white">
      <thead>
        <tr>
          <th width="140">購買時間</th>
          <th width="200">購買人</th>
          <th width="300">E-mail</th>
          <th>購買項目</th>
          <th width="120">應付金額</th>
          <th class="text-center" width="120">是否付款</th>
        </tr>
      </thead>
      <tbody>
        <tr
          v-for="item in orders"
          :key="item.id"
          :class="{'table-success':item.is_paid,'text-black':item.is_paid}"
          @dblclick="openModal(item)"
        >
          <td class="align-middle">{{item.create_at}}</td>
          <td class="align-middle">{{item.user.name}}</td>
          <td class="align-middle">{{item.user.email}}</td>
          <td class="align-middle">
            <li
              class="list-unstyled"
              v-for="product in item.products"
              :key="product.id"
            >{{ product.product.title }} 數量：{{ product.qty }}</li>
          </td>
          <td class="text-right align-middle">{{item.total | currency}}</td>
          <td class="text-center align-middle">
            <span class="text-success" v-if="item.is_paid">已付款</span>
            <span class="text-danger" v-else>未付款</span>
          </td>
        </tr>
      </tbody>
    </table>

    <div
      class="modal fade"
      id="orderModal"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>訂單更動</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-group">
                <label for="Ordername">顧客姓名</label>
                <input
                  type="text"
                  class="form-control"
                  :class="{}"
                  id="Ordername"
                  v-model="tempOrder.user.name"
                  :disabled = "tempOrder.is_paid"
                >
              </div>
              <div class="form-group">
                <label for="OrderEmail">顧客Email</label>
                <input
                  type="text"
                  class="form-control"
                  id="OrderEmail"
                  v-model="tempOrder.user.email"
                  :disabled = "tempOrder.is_paid"
                >
              </div>
              <div class="form-group">
                <span>購買項目</span>
                <div
                  class="d-flex justify-content-center"
                  v-for="product in tempOrder.products"
                  :key="product.id"
                >
                  <div class="row align-items-center">
                    <label class="text-center col-6" for="OrderItem">{{product.product.title}}</label>
                    <input
                      type="text"
                      class="form-control text-center col-2 mb-1 mr-2"
                      id="OrderItem"
                      v-model="product.qty"
                      :disabled = "tempOrder.is_paid"
                    >
                    {{product.product.unit}}
                  </div>
                </div>
              </div>
              <div class="form-group">
                <label for="OrderPrice">應付金額</label>
                <input type="text" class="form-control" id="OrderPrice" v-model="tempOrder.total" :disabled = "tempOrder.is_paid">
              </div>
              <div class="form-group form-check">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="OrderIsPay"
                  v-model="tempOrder.is_paid"
                  :disabled = "tempOrder.is_paid"
                >
                <label class="form-check-label" for="OrderIsPay">是否付款</label>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary">確認</button>
          </div>
        </div>
      </div>
    </div>

    <Pagination
      v-if="pagination.current_page != 0"
      :pageData="pagination"
      v-on:changepage="changePage"
    ></Pagination>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination";
import Alert from '../Alert';

export default {
  components: {
    Pagination,
    Alert
  },
  data() {
    return {
      orders: {
        products: {},
        user: {}
      },
      tempOrder: {
        products: {},
        user: {}
      },
      pagination: {},
      isLoading: false
    };
  },
  methods: {
    getOrder(page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/orders?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
      });
    },
    openModal(item) {
      this.tempOrder = Object.assign({}, item);
      console.log(this.tempOrder);
      $("#orderModal").modal("show");
    },
    modifyOrder(id) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/order/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.put(api, { data: vm.tempOrder }).then(response => {
        if (response.data.success) {
          vm.isLoading = false;
          $("#orderModal").modal("show");
          this.$bus.$emit("messsage:push", response.data.message, "success");
        } else {
          vm.isLoading = false;
          $("#orderModal").modal("show");
          this.$bus.$emit("messsage:push", response.data.message, "danger");
        }
      });
    },
    changePage(page) {
      this.getProducts(page);
    }
  },
  created() {
    this.getOrder();
  }
};
</script>


