<template>
  <div>
    <loading :active.sync="isLoading" color="red" backgroundColor="black">
      <template slot="default">
        <Reactor/>
      </template>
    </loading>
    <div class="top">
      <a href="#" class="topbtn">
        <i class="fas fa-arrow-alt-circle-up fa-3x text-white top-icon"></i>
      </a>
    </div>
    <div class="container">
      <News/>
      <div class="row bg-dark h-100">
        <div class="col-md-3">
          <ShopSideBar
            :CartData="cart"
            :Count="count"
            v-on:delCartItem="delCartItem"
            v-on:changeCategor="changeCategor"
          ></ShopSideBar>
          <MobileCart :CartData="cart" :cartLength="cartLength" v-on:delCartItem="delCartItem"></MobileCart>
        </div>
        <div class="col-md-9" v-if="!detailShow">
          <div class="row">
            <div class="col-md-3 my-3" v-for="item in filterProducts" :key="item.id">
              <div class="card border border-blood bg-black text-white hover">
                <div
                  class="goodsimg animated"
                  :class="{'fadeOutLeft':status.loadingItem === item.id}"
                  style="height: 200px; background-size: cover; background-position:top"
                  :style="{'backgroundImage' : `url(${item.imageUrl})`}"
                ></div>
                <div class="card-body">
                  <span
                    class="badge badge-secondary float-right ml-2 mt-1"
                    v-if="item.category === '收藏裝甲'"
                  >{{item.category}}</span>
                  <span
                    class="badge badge-primary float-right ml-2 mt-1"
                    v-else-if="item.category === '電影出現'"
                  >{{item.category}}</span>
                  <span class="badge badge-iron float-right ml-2 mt-1" v-else>{{item.category}}</span>
                  <h5 class="card-title">
                    <small class="text-white">{{item.title}}</small>
                  </h5>
                  <div class="d-flex justify-content-between align-items-baseline">
                    <del
                      class="h6"
                      v-if="item.origin_price != item.price"
                    >原價{{item.origin_price | currency}}</del>
                    <div
                      class="h5"
                      v-if="item.is_enabled"
                      :class="{'ml-auto' :'item.origin_price != item.price','text-danger' :item.origin_price != item.price}"
                    >{{item.price | currency}}</div>
                    <div class="h5 text-danger mx-auto" v-else>目前無貨</div>
                  </div>
                </div>
                <div class="card-footer d-flex p-1">
                  <button
                    type="button"
                    class="btn btn-secondary btn-sm"
                    @click="detail(item.id)"
                    :disabled="!item.is_enabled"
                  >
                    <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                    查看更多
                  </button>
                  <button
                    type="button"
                    class="btn btn-danger btn-sm ml-auto"
                    @click="addCart(item.id)"
                    :disabled="!item.is_enabled"
                  >
                    <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                    加到購物車
                  </button>
                </div>
              </div>
            </div>
          </div>
          <Pagination
            v-if="currentPage == 'all'"
            :pageData="pagination"
            v-on:changepage="changePage"
          ></Pagination>
        </div>

        <div class="col-md-9 animated" v-if="detailShow" :class="{'fadeIn':detailShow}">
          <div class="row my-5">
            <div class="col-md-7 col-12 mb-4 mb-md-0">
              <div
                style="height: 850px; background-size: cover"
                :style="{ backgroundImage:`url('${detailProduct.imageUrl}')` }"
              ></div>
            </div>
            <div class="col-md-5 col-12">
              <h2 class="h4 mb-4 text-warning">{{ detailProduct.title }}</h2>
              <span
                class="badge badge-secondary float-right ml-2 mt-1"
                v-if="detailProduct.category === '收藏裝甲'"
              >{{detailProduct.category}}</span>
              <span
                class="badge badge-primary float-right ml-2 mt-1"
                v-else-if="detailProduct.category === '電影出現'"
              >{{detailProduct.category}}</span>
              <span class="badge badge-iron float-right ml-2 mt-1" v-else>{{detailProduct.category}}</span>
              <p class="small text-white">{{detailProduct.description }}</p>
              <p class="mb-2 text-white">——</p>
              <p class="small text-white">{{detailProduct.content }}</p>
              <div class="row mt-5 text-dark">
                <div class="col-5 d-flex align-items-end">
                  <del class="h6 mb-0">原價 {{ detailProduct.origin_price | currency }} 元</del>
                </div>
                <div class="col-7 d-flex align-items-end">
                  <div class="h5 mb-0">現在只要 {{ detailProduct.price | currency }} 元</div>
                </div>
              </div>
              <select class="form-control mt-3" v-model="detailProduct.num">
                <option
                  :value="num"
                  v-for="num in 10"
                  :key="num"
                >選購 {{ num }} {{ detailProduct.unit }}</option>
              </select>
              <div class="row my-3">
                <div class="col-12 h5 text-right text-white">
                  <small>總計</small>
                  <strong>{{ detailProduct.price * detailProduct.num | currency }}</strong>
                </div>
              </div>
              <hr>
              <div class="text-right">
                <button
                  type="button"
                  class="btn btn-outline-secondary"
                  @click="detailShow = !detailShow"
                >取消</button>
                <button
                  type="button"
                  class="btn btn-iron"
                  @click="addCart(detailProduct.id, detailProduct.num)"
                >加到購物車</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <Footer/>
  </div>
</template>
<script>
import ShopSideBar from "../ShopSideBar";
import Pagination from "../Pagination";
import News from "../News";
import Reactor from "../Reactor";
import MobileCart from "../MobileCart";
import Footer from "../Footer";
export default {
  components: {
    ShopSideBar,
    Pagination,
    News,
    Reactor,
    MobileCart,
    Footer
  },
  data() {
    return {
      isLoading: false,
      products: {},
      product: {},
      detailProduct: {},
      detailShow: false,
      currentPage: "all",
      pagination: {},
      status: {
        loadingItem: ""
      },
      cart: {
        product: {}
      },
      cartLength: "",
      count: {
        all: 0,
        limited: 0,
        movie: 0,
        nonMovie: 0
      }
    };
  },
  methods: {
    getProducts(page = 1) {
      //如果有傳入用傳入值，空白為設定值
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/products?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.isLoading = false;
        vm.products = response.data.products;
        vm.pagination = response.data.pagination;
        console.log(response.data);
      });
    },
    getProductCount() {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/products/all`;
      const vm = this;
      this.$http.get(api).then(response => {
        // console.log(response.data);
        vm.product = response.data.products;
        vm.count.all = response.data.products.length;
        for (let i = 0; i < response.data.products.length; i++) {
          switch (response.data.products[i].category) {
            case "限量款式":
              vm.count.limited = vm.count.limited + 1;
              break;
            case "電影出現":
              vm.count.movie = vm.count.movie + 1;
              break;
            case "收藏裝甲":
              vm.count.nonMovie = vm.count.nonMovie + 1;
              break;
          }
        }
      });
    },
    addCart(id, qty = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      vm.isLoading = true;
      vm.status.loadingItem = id;
      const cart = {
        product_id: id,
        qty
      };
      this.$http.post(api, { data: cart }).then(response => {
        vm.status.loadingItem = "";
        vm.isLoading = false;
        vm.getCart();
        // console.log(response.data);
      });
    },
    delCartItem(id) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/cart/${id}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.delete(api).then(response => {
        vm.isLoading = false;
        vm.getCart();
        // console.log(response.data);
      });
    },
    getCart() {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      const vm = this;
      this.$http.get(api).then(response => {
        vm.cart = response.data.data;
        vm.cartLength = response.data.data.carts.length;
        console.log(response.data);
      });
    },
    changeCategor(categor) {
      const vm = this;
      vm.detailShow = false;
      vm.currentPage = categor;
    },
    changePage(page) {
      this.getProducts(page);
    },
    detail(id) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/product/${id}`;
      const vm = this;
      this.$http.get(api).then(response => {
        vm.detailProduct = response.data.product;
        vm.detailProduct.num = 1;
        vm.detailShow = true;
      });
    }
  },
  computed: {
    filterProducts() {
      var vm = this;
      if (vm.currentPage == "all") {
        return this.products;
      } else {
        return this.product.filter(function(item, index) {
          if (item.category == vm.currentPage) {
            return true;
          }
        });
      }
    }
  },
  created() {
    this.getProducts();
    this.getCart();
    this.getProductCount();
  }
};
</script>

<style scoped>
.top {
  position: fixed;
  bottom: 50px;
  right: 30px;
  z-index: 1100;
}
.top-btn {
  position: relative;
  left: 30px;
}
</style>


