<template>
  <div>
    <nav
      class="nav flex-column d-none d-md-block bg-blood shadow-lg mx-auto mt-3 text-white text-center"
    >
      <h3 class="py-2">
        <i class="fas fa-th-large mr-2"></i>商品分類
      </h3>
      <a class="nav-link text-warning" href="#" @click.prevent="changeCategor('all')">
        全部
        <span class="badge badge-warning ml-2">{{Count.all}}</span>
      </a>
      <a class="nav-link text-warning" href="#" @click.prevent="changeCategor('限量款式')">
        限量款式
        <span class="badge badge-warning ml-2">{{Count.limited}}</span>
      </a>
      <a class="nav-link active text-warning" href="#" @click.prevent="changeCategor ('電影出現')">
        電影出現
        <span class="badge badge-warning ml-2">{{Count.movie}}</span>
      </a>
      <a class="nav-link text-warning" href="#" @click.prevent="changeCategor('收藏裝甲')">
        收藏裝甲
        <span class="badge badge-warning ml-2">{{Count.nonMovie}}</span>
      </a>
    </nav>

    <nav class="nav d-block d-md-none bg-blood shadow-lg mx-auto mt-3 text-white text-center">
      <h3 class="py-2">
        <i class="fas fa-th-large mr-2"></i>商品分類
      </h3>
      <div class="d-flex">
        <a class="nav-link text-warning" href="#" @click.prevent="changeCategor('all')">
          全部
          <span class="badge badge-warning ml-2">{{Count.all}}</span>
        </a>
        <a class="nav-link text-warning" href="#" @click.prevent="changeCategor('限量款式')">
          限量款式
          <span class="badge badge-warning ml-2">{{Count.limited}}</span>
        </a>
        <a class="nav-link active text-warning" href="#" @click.prevent="changeCategor ('電影出現')">
          電影出現
          <span class="badge badge-warning ml-2">{{Count.movie}}</span>
        </a>
        <a class="nav-link text-warning" href="#" @click.prevent="changeCategor('收藏裝甲')">
          收藏裝甲
          <span class="badge badge-warning ml-2">{{Count.nonMovie}}</span>
        </a>
      </div>
    </nav>

    <nav
      class="nav flex-column d-none d-md-block bg-blood shadow-lg mx-auto mt-3 text-white text-center"
    >
      <a
        class="h3 py-2 text-white"
        data-toggle="collapse"
        href="#collapseCart"
        v-on:click=" cartFormOpen = !cartFormOpen"
      >
        <i class="fas fa-cart-arrow-down mr-2 py-2"></i>購物車
        <i class="fas fa-sort-down" :class="{'inverse':!cartFormOpen }"></i>
      </a>

      <div class="collapse show" id="collapseCart">
        <table class="table text-white table-borderless table-sm">
          <thead class="text-warning">
            <th></th>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in CartData.carts" :key="item.id" class="animated slow" :class="{'fadeOutUpBig':delItem == item.id}">
              <td class="align-middle">
                <button
                  type="button"
                  class="btn btn-outline-warning btn-sm"
                  @click="delCartItem(item.id)"
                >
                  <i class="far fa-trash-alt"></i>
                </button>
              </td>
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total | currency }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr v-if="CartData.total != 0">
              <td colspan="3" class="text-right text-warning">總計</td>
              <td class="text-right text-warning">{{ CartData.total | currency }}</td>
            </tr>
            <tr>
              <td colspan="4" class="text-right text-warning" v-if="CartData.total != 0">
                <router-link class="btn btn-iron" to="/checkout">結帳去</router-link>
              </td>
            </tr>
          </tfoot>
        </table>
      </div>
    </nav>
    
  </div>
</template>

<script>
export default {
  props: {
    Count: {},
    CartData: {
      type: Object,
      carts: {
        product: {}
      }
    }
    //將外層資料接收
  },
  data() {
    return {
      cartFormOpen: true,
      currentPage: "",
      delItem:"",
    };
  },
  methods: {
    delCartItem(id) {
      this.delItem = id;
      this.$emit("delCartItem", id);
    },
    changeCategor(category) {
      this.$emit("changeCategor", category);
    }
  }
};
</script>

<style scoped>
.inverse {
  transform: rotate(90deg);
}
</style>



