<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-dark bg-iron sticky-top">
      <router-link class="mx-auto mx-md-0" to="/">
        <img src="../assets/Stark_Industries_logo.png" height="40" alt>
      </router-link>
      <ul class="d-none d-md-inline-flex navbar-nav bg-iron mb-md-0 ml-md-auto">
        <li class="nav-item" :class="{'active':this.$route.name === 'Index'}">
          <router-link class="nav-link px-5" to="/">Home</router-link>
        </li>
        <li class="nav-item" :class="{'active':this.$route.name === 'Shop'}">
          <router-link class="nav-link px-5" to="/shop">Product</router-link>
        </li>
        <li class="nav-item" :class="{'active':this.$route.name === 'Introduce'}">
          <router-link class="nav-link px-5" to="/introduce">Introduce</router-link>
        </li>
        <li class="nav-item" :class="{'active':this.$route.name === 'Login'}">
          <router-link class="nav-link px-5" to="/Login">Admin</router-link>
        </li>
      </ul>
      <ul class="nav d-flex d-md-none mx-auto">
        <li class="nav-item" :class="{'active':this.$route.name === 'Index'}">
          <router-link class="nav-link text-white" to="/">Home</router-link>
        </li>
        <li class="nav-item" :class="{'active':this.$route.name === 'Shop'}">
          <router-link class="nav-link text-white" to="/shop">Product</router-link>
        </li>
        <li class="nav-item" :class="{'active':this.$route.name === 'Introduce'}">
          <router-link class="nav-link text-white" to="/introduce">Introduce</router-link>
        </li>
        <li class="nav-item" :class="{'active':this.$route.name === 'Login'}">
          <router-link class="nav-link text-white" to="/Login">Admin</router-link>
        </li>
      </ul>
    </nav>
  </div>
</template>
<script>
export default {
  name: "HomeNavbar",
  data() {
    return {};
  },
  created() {}
};
</script>

<style  scoped>
.navbar-nav .nav-link {
  color: #000;
  font-size: 12pt;
  border-left: 1px rgb(185, 125, 16) solid;
  border-right: 1px rgb(185, 125, 16) solid;
}
.active .nav-link {
  color: rgb(251, 202, 3) !important;
}
</style>




