<template>
  <div>
    <div class="news bg-dark text-white mt-3 mx-auto w-md-75 animated flipInX">
      <ul class="list-unstyled">
        <li class="media">
          <img
            src="https://upload.wikimedia.org/wikipedia/en/thumb/c/c4/Pepper_P.jpg/200px-Pepper_P.jpg"
            class="mr-3"
            alt="..."
          >
          <div class="media-body">
            <h4 class="mt-0 mb-1 text-warning">小辣椒 接任 史塔克工業</h4>小辣椒於2023年4月23日開始接任史塔克工業領導人
            <transition name="fade">
              <span v-show="show" class="badge badge-iron d-none d-md-inline-flex">News</span>
            </transition>
          </div>
        </li>
        <li class="media my-4">
          <img
            src="https://i.pinimg.com/originals/f5/8c/00/f58c00725c2b71885d411cfc8331a9d2.jpg"
            class="mr-3"
            alt="..."
          >
          <div class="media-body">
            <h4 class="mt-0 mb-1 text-warning">紀念史塔克先生</h4>目前推出優惠折扣碼:
            <span class="text-success">LoveYou3000</span> 享三折優惠
            <transition name="fade">
              <span v-show="show" class="badge badge-iron d-none d-md-inline-flex">News</span>
            </transition>
          </div>
        </li>
        <li class="media my-4">
          <img src="http://p3.pstatp.com/large/885a00000e99b888a67b" class="mr-3" alt="...">
          <div class="media-body">
            <h4 class="mt-0 mb-1 text-warning">Mark44 暫時無貨</h4>據說有人指出被一位身材壯碩，皮膚鮮綠，帶著眼鏡的白髮巨人帶走了，目前還沒有消息。
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
      newsShow: false
    };
  },
  mounted: function() {
    var vm = this;
    setInterval(function() {
      vm.show = !vm.show;
    }, 1000);
  }
};
</script>

<style lang="scss" scoped>
.media img {
  height: 64px;
  width: 50px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.8s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>


