<template>
  <div>
    <div class="mobileCart d-md-none">
      <button class="cart btn" type="button" data-toggle="modal" data-target="#CartModal">
        <img src="../assets/ironicon.png" height="80px" width="80px">
        <span class="cart_badge badge-pill badge-danger">{{ cartLength }}</span>
      </button>
    </div>
    <div class="modal fade bg-black" id="CartModal" tabindex="-1" role="dialog">
      <!-- 查看更多 Modal -->
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content bg-blood border">
          <div class="modal-header border-0 mx-auto">
            <h5 class="modal-title text-center text-warning" id="CartModal">購物車</h5>
            <button type="button" class="close text-white" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body bg-black">
            <table class="table text-warning table-borderless table-sm">
              <thead class="text-warning">
                <th></th>
                <th>品名</th>
                <th>數量</th>
                <th>單價</th>
              </thead>
              <tbody>
                <tr
                  v-for="item in CartData.carts"
                  :key="item.id"
                  class="animated slow"
                  :class="{'fadeOutUpBig':delItem == item.id}"
                >
                  <td class="align-middle">
                    <button
                      type="button"
                      class="btn btn-outline-warning btn-sm"
                      @click="delCartItem(item.id)"
                    >
                      <i class="far fa-trash-alt"></i>
                    </button>
                  </td>
                  <td class="align-middle">{{ item.product.title }}</td>
                  <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
                  <td class="align-middle text-right">{{ item.final_total | currency }}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr v-if="CartData.total != 0">
                  <td colspan="3" class="text-right text-warning">總計</td>
                  <td class="text-right text-warning">{{ CartData.total | currency }}</td>
                </tr>
                <tr>
                  <td colspan="4" class="text-right text-warning" v-if="CartData.total != 0">
                    <router-link class="btn btn-iron" to="/checkout">結帳去</router-link>
                  </td>
                </tr>
              </tfoot>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    CartData: {
      type: Object,
      carts: {
        product: {},
        length: ""
      }
    },
    cartLength: ""
  },
  data() {
    return {
      delItem: ""
    };
  },
  methods: {
    delCartItem(id) {
      this.$emit("delCartItem", id);
    }
  },
  created() {
    console.log(this.CartData);
  }
};
</script>

<style scoped>
.mobileCart {
  position: fixed;
  top: 100px;
  right: 30px;
  z-index: 1100;
}
.cart {
  position: relative;
  left: 30px;
}
.cart_badge {
  position: absolute !important;
  top: 4px;
  right: 4px;
}
</style>


