<template>
  <div>
    <loading :active.sync="isLoading" color="red" backgroundColor="black">
      <template slot="default">
        <Reactor/>
      </template>
    </loading>
    <div class="container h-100">
      <section class="form-row align-items-center text-center pt-3">
        <div class="col">
          <div class="alert alert-blood alert-rounded mb-0" role="alert">輸入訂單資料</div>
        </div>
        <div class="col">
          <div
            class="alert alert-rounded mb-0"
            :class="{'alert-warning':!order.is_paid,'alert-blood':order.is_paid}"
            role="alert"
          >確認資料及付款</div>
        </div>
        <div class="col">
          <div
            class="alert alert-rounded mb-0"
            :class="{'alert-warning':order.is_paid,'alert-blood':!order.is_paid}"
            role="alert"
          >完成</div>
        </div>
      </section>
      <div class="my-5 row bg-dark justify-content-center">
        <form class="col-md-6" @submit.prevent="payOrder">
          <table class="table text-white">
            <thead>
              <th>品名</th>
              <th>數量</th>
              <th>單價</th>
            </thead>
            <tbody>
              <tr v-for="item in order.products" :key="item.id">
                <td class="align-middle">{{ item.product.title }}</td>
                <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
                <td class="align-middle text-right">{{ item.final_total | currency }}</td>
              </tr>
            </tbody>
            <tfoot>
              <tr>
                <td colspan="2" class="text-right">總計</td>
                <td class="text-right">{{ order.total|currency }}</td>
              </tr>
            </tfoot>
          </table>

          <table class="table text-white">
            <tbody>
              <tr>
                <th width="150">Email</th>
                <td>{{ order.user.email }}</td>
              </tr>
              <tr>
                <th>姓名</th>
                <td>{{ order.user.name }}</td>
              </tr>
              <tr>
                <th>收件人電話</th>
                <td>{{ order.user.tel }}</td>
              </tr>
              <tr>
                <th>收件人地址</th>
                <td>{{ order.user.address }}</td>
              </tr>
              <tr>
                <th>付款狀態</th>
                <td>
                  <span v-if="!order.is_paid" class="text-danger">尚未付款</span>
                  <span v-else class="text-success">付款完成</span>
                </td>
              </tr>
            </tbody>
          </table>
          <div class="text-right" v-if="order.is_paid === false">
            <button class="btn btn-danger">確認付款去</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import Reactor from "../Reactor";
export default {
  components: {
    Reactor
  },
  data() {
    return {
      orderId: "",
      order: {
        user: {}
      }
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${
        vm.orderId
      }`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.isLoading = false;
        vm.order = response.data.order;
        console.log(response.data);
      });
    },
    payOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${
        vm.orderId
      }`;
      vm.isLoading = true;
      this.$http.post(api).then(response => {
        if (response.data.seccuss) {
          console.log(response.data);
          vm.isLoading = false;
          this.getOrder();
        } else {
          this.getOrder();
        }
      });
    }
  },
  created() {
    this.orderId = this.$route.params.id;
    this.getOrder();
  }
};
</script>


