import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Login from '@/components/pages/login'
import Dashboard from '@/components/pages/dashboard'
import Products from '@/components/pages/Products'
import Order from '@/components/pages/Order'
import Coupon from '@/components/pages/Coupon'
import CustomerOrder from '@/components/pages/CustomerOrder'
import CustomerCheckout from '@/components/pages/CustomerCheckout'

import HomePage from '@/components/View/Home'
import Index from '@/components/View/Index'
import Shop from '@/components/View/Shop'
import Checkout from '@/components/View/Checkout'
import Checkout2 from '@/components/View/Checkout2'
import Introduce from '@/components/View/Introduce'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/'
    },//避免用戶直接進入不存在的頁面
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld,
    //   meta: { requiresAuth: true }
    // },
    {
      path: '/',
      component: HomePage,
      children: [
        {
          path: '/',
          name: 'Index',
          component: Index,
        },
        {
          path: '/shop',
          name: 'Shop',
          component: Shop,
        },
        {
          path: '/login',
          name: 'Login',
          component: Login
        },
        {
          path: '/checkout',
          name: 'Checkout',
          component: Checkout
        },
        {
          path: '/checkout_check/:id',
          name: 'Checkout2',
          component: Checkout2
        },
        {
          path: '/introduce',
          name: 'Introduce',
          component: Introduce
        },
      ]
    },

    {
      path: '/admin',
      name: 'Dashboard',
      component: Dashboard,
      meta: { requiresAuth: true },
      children: [
        {
          path: 'products',
          name: 'products',
          component: Products,
          meta: { requiresAuth: true },
        },
        {
          path: 'order',
          name: 'order',
          component: Order,
          meta: { requiresAuth: true },
        },
        {
          path: 'coupon',
          name: 'coupon',
          component: Coupon,
          meta: { requiresAuth: true },
        }
      ]
    },
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard,
      children: [
        {
          path: 'customer_order',
          name: 'CustomerOrder',
          component: CustomerOrder,
        },
        {
          path: 'customer_ckeckout/:orderId',
          name: 'CustomerCkeckout',
          component: CustomerCheckout,
        },
      ],
    },
  ]
})



