<template>
  <div>
    <nav aria-label="Page navigation example" class="d-flex justify-content-center my-3">
      <ul class="pagination">
        <li class="page-item" :class="{'disabled':!pageData.has_pre}">
          <a
            class="page-link"
            href="#"
            aria-label="Previous"
            @click.prevent="changePage(pageData.current_page - 1)"
          >
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li
          class="page-item"
          v-for="page in pageData.total_pages"
          :key="page"
          :class="{'active':pageData.current_page === page}"
        >
          <a class="page-link" href="#" @click.prevent="changePage(page)">{{page}}</a>
        </li>
        <li class="page-item" :class="{'disabled':!pageData.has_next}">
          <a
            class="page-link"
            href="#"
            aria-label="Next"
            @click.prevent="changePage(pageData.current_page + 1)"
          >
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: "pagination",
  props: {
    pageData: {
      type: Object
    } //將外層資料接收
  },
  methods: {
    changePage(page) {
      this.$emit("changepage", page);
    }
  }
};
</script>


