<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right ">
      <div class="btn btn-iron mt-4" @click="openModal(true)">建立新優惠券</div>
    </div>
    <table class="table text-white mt-4">
      <thead>
        <tr>
          <th>名稱</th>
          <th class="text-center">折扣百分比</th>
          <th class="text-center">到期日</th>
          <th>是否啟用</th>
          <th>編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td class="text-center">{{item.percent}}</td>
          <td class="text-center">{{item.due_date}}</td>
          <td>
            <span class="text-success" v-if="item.is_enabled == 1">已啟用</span>
            <span class="text-danger" v-else>未啟用</span>
          </td>
          <td>
            <div class="btn btn-outline-iron" @click="openModal(false, item)">編輯</div>
          </td>
        </tr>
      </tbody>
    </table>

    <div
      class="modal fade"
      id="couponModal"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增優惠券</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <form>
              <div class="form-group">
                <label for="exampleInputCoupon">標題</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputCoupon"
                  aria-describedby="emailHelp"
                  placeholder="優惠券標題"
                  v-model="tempCoupon.title"
                >
              </div>
              <div class="form-group">
                <label for="exampleInputCode">優惠碼</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputCode"
                  placeholder="優惠碼"
                  v-model="tempCoupon.code"
                >
              </div>
              <div class="form-group">
                <label for="exampleInputDate">到期日</label>
                <input
                  type="date"
                  class="form-control"
                  id="exampleInputDate"
                  placeholder="到期日"
                  v-model="tempCoupon.due_date"
                >
              </div>
              <div class="form-group">
                <label for="exampleInputDiscount">折扣百分比</label>
                <input
                  type="text"
                  class="form-control"
                  id="exampleInputDiscount"
                  placeholder="折扣百分比"
                  v-model="tempCoupon.percent"
                >
              </div>
              <div class="form-group form-check">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="exampleCheck1"
                  v-model="tempCoupon.is_enabled"
                >
                <label class="form-check-label" for="exampleCheck1">是否啟用</label>
              </div>
            </form>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-danger"
              v-if="!isNew"
              @click="delectModel()"
            >刪除</button>
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">確認</button>
          </div>
        </div>
      </div>
    </div>

    <div
      class="modal fade"
      id="delCouponModal"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除優惠券</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除
            <strong class="text-danger">{{ tempCoupon.title }}</strong> 優惠券(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
            <button type="button" class="btn btn-danger" @click="delectCoupon">確認刪除</button>
          </div>
        </div>
      </div>
    </div>

    <Pagination
      v-if="pagination.current_page != 0"
      :pageData="pagination"
      v-on:changepage="changePage"
    ></Pagination>
  </div>
</template>

<script>
import $ from "jquery";
import Pagination from "../Pagination";
import Alert from '../Alert';

export default {
  components: {
    Pagination,
    Alert
  },
  data() {
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      isNew: false,
      isLoading: false
    };
  },
  methods: {
    getCoupon(page = 1) {
      const api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupons?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
        vm.isLoading = false;
        console.log(response.data);
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        this.tempCoupon = Object.assign({}, item); //assgin 將item寫到新的物件
        this.isNew = false;
      }
      $("#couponModal").modal("show");
    },
    updateCoupon() {
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupon`;
      let httpMethod = "post";
      const vm = this;
      if (!vm.isNew) {
        api = `${process.env.APIPATH}/api/${
          process.env.CUSTOMPATH
        }/admin/coupon/${vm.tempCoupon.id}`;
        httpMethod = "put";
      }
      this.$http[httpMethod](api, { data: vm.tempCoupon }).then(response => {
        console.log(response.data);
        if (response.data.success) {
          $("#couponModal").modal("hide");
          this.$bus.$emit("messsage:push", response.data.message, "success");
          this.getCoupon();
        } else {
          $("#couponModal").modal("hide");
          this.getCoupon();
          this.$bus.$emit("messsage:push", response.data.message, "danger");
        }
      });
    },
    delectModel() {
      $("#couponModal").modal("hide");
      $("#delCouponModal ").modal("show");
    },
    delectCoupon() {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${
        process.env.CUSTOMPATH
      }/admin/coupon/${vm.tempCoupon.id}`;
      vm.isLoading = true;
      this.$http.delete(api).then(response => {
        vm.isLoading = false;
        if (response.data.success) {
          this.$bus.$emit("messsage:push", response.data.message, "success");
          $("#delCouponModal").modal("hide");
        } else {
          this.$bus.$emit("messsage:push", response.data.message, "danger");
          $("#delCouponModal").modal("hide");
        }
      });
    },
    changePage(page) {
      this.getCoupon(page);
    }
  },
  created() {
    this.getCoupon();
  }
};
</script>


